<template>
  <div>
    <q-header elevated>
      <q-toolbar>
        <q-btn flat dense round to="/" aria-label="Menu" icon="menu" />
        <q-toolbar-title></q-toolbar-title>
        <q-space />
        <q-input
          dark
          dense
          standout
          v-model="text"
          input-class="text-left"
          class="q-mr-md"
          @keyup.enter="showDetail"
        >
          <template v-slot:prepend>
            <q-btn round dense flat icon="center_focus_weak" @click="showQR" />
          </template>
          <template v-slot:append>
            <q-icon v-if="text === ''" name="search" />
            <q-icon
              v-else
              name="clear"
              class="cursor-pointer"
              @click="text = ''"
            />
          </template>
        </q-input>
        <q-icon v-show="onLine" name="signal_wifi_0_bar" />
        <q-icon v-show="!onLine" name="signal_wifi_off" class="text-orange" />
        <q-btn flat round dense>
          <q-icon name="more_vert" />
          <q-menu>
            <div class="row no-wrap q-pa-md">
              <div class="column">
                <div class="text-h6 q-mb-md">应用设置</div>
                <q-toggle
                  v-model="$store.state.showFab"
                  label="启用浮动工具栏"
                />
                <q-toggle
                  v-model="$store.state.offlineMode"
                  label="强制在线模式"
                />
                <q-btn
                  color="primary"
                  label="更新应用"
                  @click="update"
                  push
                  size="sm"
                  v-close-popup
                />
              </div>

              <q-separator vertical inset class="q-mx-lg" />

              <div class="column items-center">
                <q-avatar size="72px">
                  <img src="../statics/profile.svg" />
                </q-avatar>
                <div class="text-subtitle1 q-mt-md q-mb-xs">{{ username }}</div>
                <q-btn
                  color="primary"
                  label="退出登录"
                  to="/login"
                  @click="logout"
                  push
                  size="sm"
                  v-close-popup
                />
              </div>
            </div>
          </q-menu>
        </q-btn>
      </q-toolbar>
      <q-toolbar inset>
        <q-btn
          size="sm"
          padding="xs"
          v-show="$route.name !== '首页'"
          flat
          @click="$router.go(-1)"
          icon="arrow_back_ios_new"
        />
        <q-toolbar-title
          ><strong>{{ $route.name }}</strong>
        </q-toolbar-title>
      </q-toolbar>
    </q-header>
  </div>
</template>

<script>
import jwt from "jsonwebtoken";
import detail from "@/components/detail.vue";
import qrcode from "@/components/qrcode.vue";

export default {
  name: "NavBar",
  mounted() {
    window.addEventListener("online", this.updateOnlineStatus);
    window.addEventListener("offline", this.updateOnlineStatus);
    let img = new Image();
    img.src = require("../statics/profile.svg");
    // console.log(require("../statics/profile.svg"));
  },
  created() {
    const token = localStorage.getItem("token");
    let restoken = jwt.decode(token);
    this.username = restoken.payload.nickname;
    this.$store.state.userInfo = restoken.payload;
  },
  data() {
    return { onLine: navigator.onLine, username: "", text: "" };
  },
  methods: {
    update() {
      this.$q
        .dialog({
          title: "应用更新",
          message: "修改项目地址",
          prompt: {
            model: `${window.location.protocol}//${window.location.host}/pda`,
            type: "text",
          },
          ok: { label: "更新" },
          cancel: { label: "取消" },
          persistent: true,
        })
        .onOk((data) => {
          console.log(">>>> OK, received", data);
          this.$init(data);
          this.$update();
        });
    },
    showQR() {
      this.$q
        .dialog({
          component: qrcode,
          parent: this,
        })
        .onOk((e) => {
          this.text = e;
          this.showDetail();
          console.log("OK");
        })
        .onCancel(() => {
          console.log("Cancel");
        })
        .onDismiss(() => {
          console.log("Called on OK or Cancel");
        });
    },
    showDetail() {
      this.$q
        .dialog({
          component: detail,
          parent: this,
          ucode: this.text,
        })
        .onOk(() => {})
        .onCancel(() => {})
        .onDismiss(() => {});
    },
    updateOnlineStatus(e) {
      const { type } = e;
      this.onLine = type === "online";
    },

    logout() {
      this.$q.notify({
        message: "退出登录成功",
        position: "top",
        type: "positive",
      });
      localStorage.clear();
    },
  },
  beforeDestroy() {
    window.removeEventListener("online", this.updateOnlineStatus);
    window.removeEventListener("offline", this.updateOnlineStatus);
  },
};
</script>

