<template>
  <div class="q-pa-md">
    <div class="q-gutter-md">
      <q-input
        dense
        :disable="mode === 'direct'"
        outlined
        v-model="install.old_identity_code"
        autocapitalize="off"
        label="已上道设备编号"
        @keyup.enter="getOld"
      />
      <q-input
        dense
        outlined
        v-model="install.new_identity_code"
        autocapitalize="off"
        label="待上道设备编号"
        @keyup.enter="getNew"
      />
      <div class="q-gutter-sm row" v-if="mode !== 'direct'">
        <q-radio
          class="col"
          dense
          v-model="mode"
          val="cycle"
          label="周期修上道"
        />
        <q-radio
          class="col"
          dense
          v-model="mode"
          val="emergency"
          label="应急上道"
        />
        <q-radio
          class="col"
          dense
          v-model="mode"
          val="fault"
          label="故障修上道"
        />
        <q-radio
          class="col"
          dense
          v-model="mode"
          val="direct"
          label="直接上道"
        />
      </div>
      <q-input
        dense
        outlined
        v-model="install.maintain_location_code"
        autocapitalize="off"
        label="直接上道位置"
        v-if="mode == 'direct'"
        ><template v-slot:after>
          <q-btn
            color="primary"
            label="切换模式"
            @click="
              mode = 'cycle';
              install.maintain_location_code = '';
            "
          />
        </template>
      </q-input>
      <device type="old" :item="olddevice" />
      <device type="new" :item="newdevice" />
      <div class="q-pt-md column">
        <q-btn color="primary" icon="mail" label="上道" @click="makeInstall" />
      </div>
    </div>
  </div>
</template>

<script>
import Device from "@/components/device.vue";
export default {
  components: { Device },
  data() {
    return {
      shape: "line",
      mode: "cycle",
      modes: ["cycle", "emergency", "fault"],
      install: {
        new_identity_code: "",
        old_identity_code: "",
        maintain_location_code: "",
      },
      olddevice: {},
      newdevice: {},
    };
  },
  methods: {
    makeInstall() {
      let req = {
        type: this.mode,
        date: this.$date.formatDate(Date.now(), "YYYY-MM-DD HH:mm:ss"),
        ...this.install,
      };
      this.$http
        .post("install", req)
        .then((res) => {
          this.$q.notify({
            type: "positive",
            position: "top",
            message: res.data.msg,
          });
        })
        .catch(({ response }) => {
          this.$q.notify({
            type: "warning",
            position: "top",
            message: response.data.msg,
          });
        });
    },
    getOld() {
      this.$http
        .post("installOfSearch", {
          code: this.install.old_identity_code,
        })
        .then((res) => {
          this.olddevice = res.data.data[0];
        });
    },
    getNew() {
      this.$http
        .post("installOfSearch", {
          code: this.install.new_identity_code,
        })
        .then((res) => {
          this.newdevice = res.data.data[0];
        });
    },
  },
};
</script>

