import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Login from '../views/Login.vue'
import Regsiter from '../views/Regsiter.vue'
import Search from '../views/Search.vue'
import Store from '../views/store/store.vue'
import Scenestore from '../views/store/scenestore.vue'
import Scrap from '../views/Scrap/Scrap.vue'
import Damage from '../views/Scrap/Damage.vue'
import Check from '../views/Check/Check.vue'
import DepotOut from '../views/depot/depotOut.vue'
import DepotIn from '../views/depot/depotIn.vue'
import Install from '../views/Install/Install.vue'
import Uninstall from '../views/Install/Uninstall.vue'
import Bind from '../views/Bind/bind.vue'
import Test from '../views/test.vue'
import TaskHome from '../views/Task/Home.vue'
import Task from '../views/Task/Task.vue'
import Arrange from '../views/Task/Arrange.vue'
import Return from '../views/Task/Return.vue'
import Acceptance from '../views/Task/Acceptance.vue'
import Complete from '../views/Task/Complete.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '首页',
    component: Home
  },
  {
    path: '/login',
    name: '登录',
    component: Login
  },
  {
    path: '/regsiter',
    name: '注册',
    component: Regsiter
  },
  {
    path: '/search',
    name: '设备详情查询',
    class: "bg-grey-4",
    component: Search,
    icon: 'search'
  },
  {
    path: '/task/list/:id',
    name: '新站任务详情',
    class: "bg-grey-4",
    component: Task,
    hidden: true
  },
  {
    path: '/task/home',
    name: '新站任务',
    class: "bg-grey-4",
    component: TaskHome,
    icon: 'assignment'
  },
  {
    path: '/task/arrange/:id',
    name: '分配检修任务',
    class: "bg-grey-4",
    component: Arrange,
    hidden: true
  },
  {
    path: '/task/acceptance/:id',
    name: '设备验收',
    class: "bg-grey-4",
    component: Acceptance,
    hidden: true
  },
  {
    path: '/task/return/:id',
    name: '现场退回',
    class: "bg-grey-4",
    component: Return,
    hidden: true
  },
  {
    path: '/task/complete/:id',
    name: '检修完成',
    class: "bg-grey-4",
    component: Complete,
    hidden: true
  },
  {
    path: '/install',
    name: '上道',
    class: "bg-grey-4",
    component: Install,
    icon: 'file_upload'
  },
  {
    path: '/uninstall',
    name: '下道',
    class: "bg-grey-4",
    component: Uninstall,
    icon: 'get_app'
  },
  {
    path: '/about',
    name: '安卓接口测试',
    class: "bg-grey-4",
    component: About,
    hidden: true
  },
  {
    path: '/store',
    name: '入库',
    class: "bg-grey-4",
    component: Store,
    icon: 'exit_to_app'
  },
  {
    path: '/scenestore',
    name: '现场备品入库',
    class: "bg-grey-4",
    component: Scenestore,
    icon: 'exit_to_app'
  },
  {
    path: '/scrap',
    name: '报废',
    class: "bg-grey-4",
    component: Scrap,
    icon: 'delete_sweep'
  },
  {
    path: '/check',
    name: '盘点',
    class: "bg-grey-4",
    component: Check,
    icon: 'fact_check'
  },
  {
    path: '/depotin',
    name: '入所',
    class: "bg-grey-4",
    component: DepotIn,
    icon: 'move_to_inbox'
  },
  {
    path: '/depotout/:id?',
    name: '出所',
    class: "bg-grey-4",
    component: DepotOut,
    icon: 'unarchive'
  },
  {
    path: '/damage',
    name: '报损',
    class: "bg-grey-4",
    component: Damage,
    icon: 'rule'
  },
  {
    path: '/bind',
    name: '现场设备定位',
    class: "bg-grey-4",
    component: Bind,
    hidden: true
  },
  {
    path: '/test',
    name: '测试',
    class: "bg-grey-4",
    component: Test,
    icon: 'bug_report',
    hidden: true
  },

]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token');
  if (to.path !== '/login' && to.path !== '/regsiter' && token == null) next({ path: '/login' })
  else {
    Vue.prototype.$http.defaults.headers.common['Token'] = token
    next()
  }
})
export default router
