<template>
  <div>
    <q-list bordered separator>
      <q-slide-item
        v-for="(item, index) in items"
        :key="index"
        @left="onLeft($event, index)"
        @right="onRight($event, index)"
      >
        <template v-if="slide" v-slot:left>
          <q-icon name="done" />
        </template>
        <template v-if="slide" v-slot:right>
          <q-icon name="alarm" />
        </template>

        <q-item
          v-ripple
          clickable
          @click="showDetail(item.identity_code)"
          :active="item.active"
          active-class="bg-teal-1 text-grey-8"
        >
          <q-item-section>
            {{ item.identity_code }}{{ item.unique_code }}
            <q-item-label
              >{{ item.category_name }} {{ item.model_name }}
              {{ item.sub_model_name }}</q-item-label
            >
          </q-item-section>
          <q-item-section v-if="item.location_name">
            {{ item.location_name }}
          </q-item-section>
          <q-item-section v-if="item.source">
            {{ item.source }}
          </q-item-section>
          <q-item-section v-if="item.check">
            {{ item.check }}
          </q-item-section>
          <q-item-section
            thumbnail
            class="text-black"
            style="width: 55px; padding-right: 10px"
            v-if="!item.status == ''"
          >
            {{ item.status }}
          </q-item-section>
          <q-btn
            v-if="AshowDelete"
            size="12px"
            flat
            dense
            round
            icon="delete"
            side
            @click.stop="removeItem(index)"
          />
        </q-item>
      </q-slide-item>
    </q-list>
  </div>
</template>

<script>
import detail from "@/components/detail.vue";
export default {
  name: "simpleList",
  props: {
    Litems: { default: [] },
    showDelete: { default: true },
    slidable: { default: true },
  },
  computed: {
    items() {
      return this.Litems;
    },
    AshowDelete() {
      return this.showDelete;
    },
    slide() {
      return this.slidable;
    },
  },
  methods: {
    onLeft({ reset }, index) {
      // this.$q.notify("Left action triggered. Resetting in 1 second.");
      this.finalize(reset);
      this.Litems[index].active = true;
    },

    onRight({ reset }, index) {
      // this.$q.notify("Right action triggered. Resetting in 1 second.");
      this.finalize(reset);
      this.Litems[index].active = false;
    },

    finalize(reset) {
      this.timer = setTimeout(() => {
        reset();
      }, 200);
    },
    removeItem(index) {
      this.$q
        .dialog({
          title: "确认删除？",
          message: `删除设备： ${this.items[index].identity_code}`,
          ok: { color: "negative" },
          cancel: true,
          persistent: true,
        })
        .onOk(() => {
          this.items.splice(index, 1);
        })
        .onCancel(() => {})
        .onDismiss(() => {});
    },
    showDetail(item) {
      this.$q
        .dialog({
          component: detail,
          parent: this,
          ucode: item,
        })
        .onOk(() => {})
        .onCancel(() => {})
        .onDismiss(() => {});
    },
  },
  beforeDestroy() {
    clearTimeout(this.timer);
  },
};
</script>

